<template>
  <div class="grayPageBG">
    <van-nav-bar
      ref="topBar"
      title="任务详情"
      right-text="接单规则"
      left-text="返回"
      fixed
      left-arrow
      @click-left="onNavBarLeftPressed"
      @click-right="onNavBarRightPressed"/>
    <div class="under-nav overflow-auto" :style="containerHeight" v-if="missionDetail">
      <div v-if="missionDetail" style="background-color: white">
        <div>
          <span>标 题：</span>
          <span>{{missionDetail.title}}</span>
        </div>
        <div style="display: flex;align-items: center;flex-wrap: wrap">
          <span>发 布 方：</span>
          <img class="head-img" :src="imgFullPath(missionDetail.publishUserImgUrl)" alt=""/>
          <span>{{missionDetail.publishUserName}}</span>
          <van-tag size="large" type="success" style="display: flex;align-items: center" v-if="missionDetail.deposit">
            <icon name="bao" scale="3"></icon>
            {{missionDetail.deposit / 100}}元
          </van-tag>
          <van-button plain type="danger" size="small">+关注</van-button>
          <van-button plain type="danger" size="small">店铺</van-button>
        </div>
        <div>
          <span>编 号：</span>
          <span>{{missionId}}</span>
          <span class="category">类 别：</span>
          <span>{{getMissionTypeName()}}</span>

        </div>
        <div>
          <span>单 价：</span>
          <span>{{missionDetail.price / 100}}元</span>
        </div>
        <div>
          <span>文字验证：</span>
          <span>{{missionDetail.textVerify}}</span>
        </div>
        <div v-if="missionDetail.url">
          <span>链接：</span>
          <a :href="missionDetail.url"><span style="text-decoration: underline;">点击查看任务链接</span></a>
        </div>
        <div>
          <span>备 注：</span>
          <span>{{missionDetail.remark}}</span>
        </div>
        <div>
          <span>置顶到期：</span>
          <span>{{missionDetail.topEndTime}}</span>
        </div>
        <div>
          <span>截止时间：</span>
          <span>{{missionDetail.deadlineTime}}</span>
        </div>
      </div>
      <div class="align-center">
        <div>
          <span>验证图</span>
          <span>(共{{missionDetail.sampleImgList.length}}张)</span>
        </div>
        <div v-for="(item,index) in missionDetail.sampleImgList" :key="'sample' + index">
          <img class="screen-img" :src="imgFullPath(item)" alt=""/>
          <div>
            <span>图{{index + 1}}</span>
          </div>
        </div>
        <div class="text-section">
          <h3>操作说明</h3>
          <van-icon name="arrow-down"/>
        </div>
        <div v-for="(item,index) in missionDetail.explainImgList" :key="'explain' + index">
          <img class="screen-img" :src="imgFullPath(item)" alt=""/>
          <div>
            <span>图{{index + 1}}</span>
          </div>
        </div>
      </div>
    </div>

    <div class="submit" ref="bottomBar">
      <van-button type="danger" round size="large" class="left-button" @click="onNavBarLeftPressed">放弃</van-button>
      <van-button type="primary" round size="large" @click="onAcceptMissionButtonPressed">抢单</van-button>
    </div>
  </div>
</template>

<script>
  import { ACCEPT_MISSION_URL, MISSION_DETAIL_URL } from "@/config/host"
  import { getPropertyById } from "@/service/property"

  export default {
    name: "MissionDetail",
    data () {
      return {
        missionId: null,
        missionDetail: null,
        containerHeight: {
          height: "100%"
        }
      }
    },
    props: {},
    async mounted () {
      this.missionId = this.$route.query.id
      const result = await this.httpPostWithLoading(MISSION_DETAIL_URL, { id: this.missionId })
      if (result.success) {
        this.missionDetail = result.data
      }
      this.containerHeight.height = window.innerHeight - this.$refs.topBar.offsetHeight - this.$refs.bottomBar.offsetHeight + "px"
    },
    methods: {
      onNavBarLeftPressed () {
        this.$router.back()
      },
      onNavBarRightPressed () {
      },
      async onAcceptMissionButtonPressed () {
        const result = await this.httpPostWithLoading(ACCEPT_MISSION_URL, { id: this.missionId })
        if (result.success) {
          this.toastSuccess("抢单成功")
          this.$store.commit("userNeedRemoveItemId", this.missionId)
          this.$router.back()
        }
      },
      getMissionTypeName () {
        const p = getPropertyById(this.missionDetail.typePropertyId)
        return p.name
      }

    }
  }
</script>

