  .head-img {
    width: 36px;
    height: 36px;
    border-radius: 18px;
  }

  .info-container {
    background-color: white;
  }

  .container {
    height: 300px;
  }

  .screen-img {
    width: 300px;
  }

  .flex-column-center {
    flex-direction: column;
    display: flex;
    justify-content: center;
  }

  .align-center {
    text-align: center;
  }

  .text-section {
    background-color: white;
    padding: 10px 0;
    margin-bottom: 20px;
  }

  .submit {
    display: flex;
    background-color: white;
    justify-content: center;
    padding: 10px 40px;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;

    .left-button {
      margin-right: 40px
    }
  }
</style>
